.App {
  text-align: center;
  background-color: #222;
  min-height: 100vh;
  font-size: calc(12px + 0.5vmin);
  color: white;
}

div.homeInputCol {
  display: inline-block;
  width: calc(80px + 8vw);
  margin: 0; 
  padding-left: calc(10px + 1vw);
  padding-right: calc(10px + 1vw);
}

div.centralCol {
  width: calc(200px + 25vw);
  margin: auto;
  background-color: #222;
  padding: 10px;
  /* background-color: aquamarine; */
}

div.homeInputCol h3 {
  margin-top: 0;
}

input, select {
  font-size: 1.1em;
  border-radius: 5px;
  padding: 5px;
}

input[type=button], input[type=submit] {
  background-color: #171;
  border: solid 1px #171;
  font-weight: bold;
  color: #FFF;
}

input[type=button]:disabled, input[type=submit]:disabled {
  background-color: rgb(70, 119, 70);
  border-color: rgb(70, 119, 70);
}

input[type=text] {
  background-color: #FFF;
  border: solid 1px #AAA;
}

input[type=text].invalid {
  background-color: rgb(236, 200, 200);
}

.error {
  color: rgb(236, 200, 200);
}

div.labelledInputContainer {
  margin-top: 15px;
}

div.labelledInputLabelContainer {
  text-align: left;
  font-size: .8em;
  margin-bottom: .5em;
}

div.homeInputCol input[type=text] {  
  width: calc(100% - 15px); 
}

div.homeInputCol select, div.homeInputCol input[type=button] {  
  width: 100%; 
}

